*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.root,
.loader{
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    align-items: center;
    justify-content: center;
}
.loader_text{
    text-align: center;
}

.container{
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: rgb(255, 230, 230);
}

.movie{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    padding: 10px;
}
.movie img{
    border-radius: 10px;
    margin: 0 auto;
    width: 150px;
}
.movie_data{
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    border: 1px solid rgb(252, 185, 185);
    border-radius: 5px;
}

.movie_title{
    font-size: 1.5rem;
    text-align: center;
}
.movie_year{
    text-align: center;
}
.genres{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.8rem;
    color: rgba(0, 0, 0, 0.397);
    list-style-type: none;
}
.genres li{
    float: left;
    margin: 5px 10px;
}
.movie_summary{
    color: rgba(0, 0, 0, 0.685);
    padding: 10px;
    font-style: italic;
}

@media only screen and (min-width: 768px){
    .movies{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .movie{
        position: relative;
        flex-direction: row;
        margin-top: 100px;
        width: 85%;
        padding-left: 140px;
    }
    .movie img{
        position: absolute;
        top: -70px;
        left: 0;
    }
    .movie_summary{
        padding: 10px 40px;
    }
}

@media only screen and (min-width: 1024px){
    .movies{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .movie{
        position: relative;
        flex-direction: row;
        margin-top: 100px;
        width: 85%;
        padding-left: 0px;
    }
    .movie_data{
        padding: 25px 10px;
    }
    .movie img{
        position: relative;
        width: 200px;
    }
    .movie_summary{
        padding: 10px 40px;
    }
    .movie_title{
        font-size: 2.5rem;
    }
    .movie_year{
        font-size: 1.4rem;
    }
    .genres{
        font-size: 1rem;
    }
    .movie_summary{
        font-size: 1.4rem;
    }
}